.control {
  &:hover .control__mark {
    border-color: #bababa;
  }

  @media (min-width: $tablet-width) {
    font-size: 20px;
    line-height: 30px;
  }
}

.control--radio {
  color: #222222;
  font-size: 16px;

  @media (min-width: $tablet-width) {
    font-size: 20px;
  }
}

.control--checkbox {
  color: #000000;
}

.control__mark {
  position: absolute;
  left: 0;
  top: calc(50% - 15px);

  width: 30px;
  height: 30px;

  border: 1px solid #cdcdcd;

  @media (min-width: $tablet-width) {
    top: calc(50% - 20px);

    width: 40px;
    height: 40px;
  }
}

.control__mark--radio {
  border-radius: 50%;
}

.control__input:disabled + .control__mark {
  border-color: #bababa;

  opacity: 0.3;
}

.control__input:disabled ~ .control__label {
  opacity: 0.3;
}

.control__input[type="radio"]:checked + .control__mark--radio::before {
  content: "";
  position: absolute;
  left: calc(50% - 6.75px);
  top: calc(50% - 6.75px);

  width: 13.5px;
  height: 13.5px;

  background-color: #68b738;
  border-radius: 50%;

  @media (min-width: $tablet-width) {
    top: calc(50% - 9px);
    left: calc(50% - 9px);

    width: 18px;
    height: 18px;
  }
}

.control__input[type="checkbox"]:checked + .control__mark--checkbox::before {
  content: "";
  position: absolute;
  left: 0;
  top: calc(50% - 14.5px);

  width: 29px;
  height: 29px;

  background-image: url("../../img/icons/checked.svg");
  background-repeat: no-repeat;
  background-position: center;

  @media (min-width: $tablet-width) {
    top: calc(50% - 19.5px);

    width: 39px;
    height: 39px;

    background-image: url("../../img/icons/checked-big.svg");
  }
}
